h1 {
  font-family: "Arial Black", "Helvetica", Sans-Serif;
  color: rgb(255, 145, 0);
  text-align: center;
  text-shadow: 3px 1px 1px rgb(175, 85, 0);
}
p {
  padding: 0;
  margin: auto;
  min-width: 30px;
  max-width: 50px;
}
.plus, .minus, .del {
  cursor: pointer;
  font-weight: bolder;
}
.plus, .minus {
  background: rgb(238, 238, 238);
}
.plus {
  color: red;
  border-radius: 6px 6px 2px 2px;
}
.minus {
  color: rgb(0, 63, 238);
  border-radius: 2px 2px 6px 6px;
}
.del img {
  max-width: 24px;
}
.productImg img {
  max-width: 100px;
  border-radius: 5px;
}
table {
  font-family: "Helvetica", "Arial", Sans-Serif;
  margin: auto;
  font-size: 14px;
  border-spacing: 0 5px;
  text-align: center;
  cursor: default;
  user-select: none;
}
th {
  font-family: "Arial Black", Sans-Serif;
  background: linear-gradient(to top, rgb(255, 200, 82), rgb(255, 192, 109) 40%, #ffd45f);
  color: white;
  text-shadow: 1px 1px 1px #2D2020;
  padding: 10px 20px;
}
th:first-child {
  border-radius: 15px 0 0 5px;
}
th:last-child {
  border-radius: 0 15px 5px 0;
}
td {
  padding: 5px 5px 0 5px;
  border-top: 1px solid #c0c0c0;
  border-bottom: 1px solid #c0c0c0;
  background: white;
}
tr td:first-child{
  border-left: 1px solid #c0c0c0;
  border-radius: 5px 0 0 5px;
}
tr td:last-child{
  border-right: 1px solid #c0c0c0;
  border-radius: 0 5px 5px 0;
}
.total {
  font-size: 18px;
}
.cart-empty {
  font-family: "Arial Black", Sans-Serif;
  color: crimson;
  font-size: 18px;
  border: none;
}